import React, {Component} from 'react';
import {Card, Table} from 'antd';
import Filter from "./Filter";
import {postAction} from "../../../../axios";


let typeLists = [
    { code: null, name: '全部' },
    { code: 1, name: '失联预警' },
    { code: 2, name: '网络预警' },
    { code: 3, name: '挂科预警' },
    { code: 4, name: '消费预警'},
    { code: 5, name: '夜归预警'},
    { code: 6, name: '逃课预警'},
];
//
// let resultLists = [
//     { code: 0, name: '未处理' },
//     { code: 1, name: '已处理' }
// ]

export default class PersonalHistory extends Component {

    constructor(props){
        super(props);
        this.state = {
            data:[
                // {
                //     key: '1',
                //     sortIndex: 1,
                //     time: '2018/01/01 ~ 2018/02/01',
                //     type: "网络预警",
                //     level: "红色预警",
                //     describe: "描述描述描述",
                //     dealTime: "2018/01/03",
                //     results: "已处理",
                //     note: "备注备注备注",
                // }
            ],
            pagination: {
                current: 1, // 当前页数，
                pageSize: 10, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            },
            filter:{
                type: null,
                level: null,
                startDate: "",
                endDate: ""
            }
        }
        this.getFilterData = this.getFilterData.bind(this);
    }

    getFilterData(filter,isHandle) {
        if(isHandle){
            let data = Object.assign({},{pageInfo:{pageNum:this.state.pageNum,pageSize:this.state.pageSize}},this.state.filter)
            this.getData(data)
            return;
        }
        this.setState({filter},()=>{
            let data = Object.assign({},{pageInfo:{pageNum:this.state.pageNum,pageSize:this.state.pageSize}},filter)
            // console.log(data)
            this.getData(data)
        })
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getData();
            }
        )
    };

    //转换时间
    formatDateTime(timeStamp) {
        let date = new Date(timeStamp);
        let y = date.getFullYear();
        let m = date.getMonth() + 1;
        let d = date.getDate();
        let h = date.getHours();
        let i = date.getMinutes();
        let s = date.getSeconds();
        m = m < 10 ? ('0' + m) : m;
        d = d < 10 ? ('0' + d) : d;
        h = h < 10 ? ('0' + h) : h;
        i = i < 10 ? ('0' + i) : i;
        s = s < 10 ? ('0' + s) : s;
        return y + '-' + m + '-' + d+' '+ h + ':' + i + ':' + s;
    }

    getData () {

        const {type, level, startDate, endDate} = this.state.filter;
        let data = {
            student:{
                studentNo: this.props.studentNo
            },
            alarmType: type,
            alarmLevel: level,
            startDate: startDate,
            endDate: endDate,
            status: 1,
            pageInfo:{
                pageNum: this.state.pagination.current,
                pageSize: this.state.pagination.pageSize
            }
        }

        postAction(
            "/bigdata/alarmTrigger/getPageData",data
        ).then(
            (res) => {
                if (res.success) {
                    let pageInfo = res.obj.list;
                    let newData = [];

                    pageInfo.forEach(
                        (item, index) => {
                            newData.push({
                                key: index + 1,
                                sortIndex: index + 1,
                                people: item.handleUser,
                                time: item.createDate,
                                type: item.alarmType,
                                level: item.alarmLevel,
                                describe: item.alarmDepict,
                                dealTime: item.handleTime ? this.formatDateTime(item.handleTime) : "",
                                results: item.handleResult,
                                note: item.handleRemark,
                            })
                        }
                    )
                    this.setState({
                        data:newData,
                        pagination: {
                            current: res.obj.pageNum, // 当前页数，
                            pageSize: res.obj.pageSize, // 每页条数
                            total: res.obj.total, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        },
                    })
                } else {
                    this.setState({
                        data:[],
                        pagination:{
                            current: 1, // 当前页数，
                            pageSize: 10, // 每页条数
                            total: 0, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                }
            }
        )
    }

    render () {
        const colors = ['#e45a5a','#f5a623','#f8e71c','#309cff'];

        // 表头设置
        const columns = [
            {
                title: '序号',
                dataIndex: 'sortIndex',
                width: '5%'
            },
            {
                title: '处理人',
                dataIndex: 'people',
                width: '9%',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '预警时间',
                dataIndex: 'time',
                width:"14%",
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '预警类别',
                dataIndex: 'type',
                width:"9%",
                render:(text, record)=>{
                    let val = typeLists[text] ? typeLists[text].name:'';
                    return (<span title={val}>{val}</span>)
                }
            },
            {
                title: '预警级别',
                dataIndex: 'level',
                width:"9%",
                render:(text, record)=>{
                    const backgroundColor = colors[text-1];
                    return (
                        <span className={'circle-mark'} style={{width:16,height:16,top:4,left:"40%",backgroundColor,    display: "block", borderRadius: "50%",position: "relative"}}/>
                    )
                }
            },
            {
                title: '预警描述',
                dataIndex: 'describe',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '处理时间',
                dataIndex: 'dealTime',
                width:"18%",
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '处理结果',
                dataIndex: 'results',
                width:"8%",
                render: (text, record) => {
                    return (<span title={text}>{text}</span>)
                }
            },
            {
                title: '处理备注',
                dataIndex: 'note',
                width: "8%",
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            }
        ]

        return (
            <Card
                title={<div className="common-title">个人历史预警查询</div>}
                headStyle={{border:"none"}}
                bodyStyle={{paddingTop: 0}}
            >
                <Filter getFilterData={(data) => this.getFilterData(data)} />
                <Table
                    columns={columns}
                    dataSource={this.state.data}
                    pagination={this.state.pagination}
                    onChange={this.handleTableChange}
                    bordered={true}
                    size="middle"
                />
            </Card>
        )
    }

    componentDidMount() {
        this.getData()
    }

}


