import React, { Component } from 'react';
import { Card, Table, message, Button} from 'antd';
import HandleStateModel from '../HandleStateModel';
import {postAction} from '../../../axios';

let typeLists = [
    {code: 1, name: '失联预警'},
    {code: 2, name: '网络预警'},
    {code: 3, name: '挂科预警'},
    {code: 4, name: '消费预警'},
    {code: 5, name: '夜归预警'},
    {code: 6, name: '逃课预警'},
];

let markStyle= {
    display: 'inline-block',
    background: '#e45a5a',
    width:'10px',
    height: '10px',
    borderRadius: '100%',
    position: 'relative'
}

class CurEarlyWarnings extends Component {
    constructor(props) {
        super(props);
        const colors = ['#e45a5a','#f5a623','#f8e71c','#309cff'];
        const columns = [{
            title: '序号',dataIndex: 'rowIndex',align:'center',width: "10%"
        }, {
            title: '预警时间',dataIndex: 'createDate',align:'center',width: "30%"
        }, {
            title: '预警描述',dataIndex: 'alarmDepict',align:'center',width: "20%",render: (text) => {return (<span title={text}>{text}</span>)}
        }, {
            title: '预警级别',dataIndex: 'alarmLevel',align:'center',
            render:(text, record)=>{
                const backgroundColor = colors[text-1];
                let styles = Object.assign({},markStyle,{backgroundColor})
                return (
                    <span className={'circle-mark'} style={styles}/>
                )
            }
        }, {
            title: '预警类别',dataIndex: 'alarmType',align:'center',
            render:(text, record)=>{
                let val = typeLists[text-1] ? typeLists[text-1].name:'';
                return val
            }
        }, {
            title: '状态',dataIndex: 'status',align:'center',
            render:(text, record)=>{
                let color,val;
                if(parseInt(text,10)===0){
                    color = '#e45a5a';val = '待处理';
                } else {
                    color = '#71cf09';val = '已处理';
                }
                return (<div style={{color,cursor:"pointer"}}>{val}</div>);
            },
         onCell:(record)=>{
             return {
                 onClick:(e)=>this.handleClick(e,record)
             }
         }
        }];
        this.state = ({
            columns,
            alarmDetails:[],
            page: 1,
            pageSize: 5,
            total: 0,
            visible: false,
            curRowInfo:undefined
        });
    }

    componentWillMount(){
        this.getData(this.props.studentNo);
    }
    getData(studentNo){
        let data = {
            student:{studentNo},
            pageInfo: {
                pageNum: this.state.page,
                pageSize: this.state.pageSize,
            }
        }
        postAction("/bigdata/alarmTrigger/getStudentAlarmByYear", data).then(
            (res) => {
                if(res.success) {
                    this.setState({
                        alarmDetails:res.obj.list || [],
                        total:res.obj.total
                    })
                } else {
                    message.error(res.msg);
                }
            }
        )
    }
    handleClick(e,record){
        e.stopPropagation();
        if(record.status===0){
            this.setState({visible:true,curRowInfo:record});
        }
    }
    handleState = ()=> {
        this.setState({visible:false},()=>this.getData(this.props.studentNo))
    }
    changePage = (page,pageSize) => {
        this.setState({page,pageSize},()=>this.getData(this.props.studentNo));
    }
    render(){

        const data = this.state.alarmDetails || [];
        data.forEach((item,index)=>{
            data[index].key = index;
            data[index].rowIndex = index+1;
        })
        return (
            <Card>
                <div className="common-title" style={{float:"left",marginBottom:"20px"}}>当前预警事件</div>
                <Button type='primary' style={{float:"right",marginBottom:"20px"}} href="#/mainframe/comprehensivewarning">返回</Button>
                <div style={{marginTop:20}}>
                    <Table
                        columns={this.state.columns}
                        dataSource={data}
                        bordered
                        size="middle"
                        pagination={
                            {
                                total:this.state.total,
                                pageSize:this.state.pageSize,
                                onChange:this.changePage,
                                // showSizeChanger: true,
                                // onShowSizeChange: this.changePage,
                                // pageSizeOptions: ['5','10','20','30','40'],
                                showQuickJumper: true,
                                showTotal: total => `共 ${total} 条数据`
                            }
                        }
                    />
                </div>
                {this.state.visible?(
                    <HandleStateModel 
                        alarmTriggerId={this.state.curRowInfo&&this.state.curRowInfo.id}
                        onCancel={()=>this.setState({visible:false})}
                        onOk={this.handleState}
                    />
                ):null}
            </Card>
        )
    }
}

export default CurEarlyWarnings;


