import React, {Component} from 'react';
import {Select, DatePicker, Button} from 'antd';


const { Option } = Select;
const RangePicker = DatePicker.RangePicker;

export default class Filter extends Component {

    constructor(props){
        super(props);
        this.state = {
            startDate: undefined,
            endDate: undefined,
            type:null,
            typeLists:[
                { code: null, name: '全部' },
                { code: 1, name: '失联预警' },
                { code: 2, name: '网络预警' },
                { code: 3, name: '挂科预警' },
                { code: 4, name: '消费预警'},
                { code: 5, name: '夜归预警'},
                { code: 6, name: '逃课预警'},
            ],
            level:null,
            levelLists:[
                { code: null, name: '全部' },
                { code: 1, name: '红色预警' },
                { code: 2, name: '橙色预警' },
                { code: 3, name: '黄色预警' },
                { code: 4, name: '蓝色预警' }
            ],
        }
    }

    changeFilter (value, num) {
        switch (num) {
            case 0:
                this.setState({
                    startDate: value[0],
                    endDate: value[1]
                })
                break;
            case 1:
                this.setState({
                    type: value
                })
                break;
            case 2:
                this.setState({
                    level: value
                })
                break;
            default:
                break;
        }
    }

    // 筛选条件搜索按钮
    sureBtn () {
        let filter = {
            startDate: this.state.startDate,
            endDate: this.state.endDate,
            type: this.state.type,
            level: this.state.level
        }
        this.props.getFilterData(filter);
    }

    render () {
        return (
            <div style={{marginBottom: 20}}>
                <span>预警时间：</span>
                <RangePicker
                    style={{ width: '280px' }}
                    onChange={(moment, values) => {
                        this.changeFilter(values, 0)
                    }} />

                <span style={{marginLeft:"15px"}}>预警类别：</span>
                <Select
                    style={{ width: '150px' }}
                    placeholder="选择预警类别"
                    value={this.state.type}
                    onChange={(value) => { this.changeFilter(value, 1) }}
                >
                    {this.state.typeLists.map((item, index) => <Option key={index} value={item.code}>{item.name}</Option>)}
                </Select>

                <span style={{marginLeft:"15px"}}>预警级别：</span>
                <Select
                    style={{ width: '150px' }}
                    placeholder="选择预警级别"
                    value={this.state.level}
                    onChange={(value) => { this.changeFilter(value, 2) }}
                >
                    {this.state.levelLists.map((item, index) => <Option key={index} value={item.code}>{item.name}</Option>)}
                </Select>

                <Button style={{marginLeft:"15px"}} type="primary" icon="search" onClick={() => this.sureBtn()}>搜  索 </Button>
            </div>
        )
    }
}


