import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Card, Row, Col, message } from 'antd';
import CurEarlyWarnings from './CurEarlyWarnings';
import LineChartCard from '../../../common/components/BizChartsCard/LineChartCard';
import Info from './Info';
import {postAction} from '../../../axios';
import PersonalHistory from "./PersonalHistory/index";

class EarlyWarningDetail extends Component {
    state = {
        studentInfo:{},
        studentNo:undefined,
        historyAlarms:[]
    }
    componentWillMount() {
        if(this.props.match.params && this.props.match.params.id){
            let studentNo = this.props.match.params.id;
            this.setState({studentNo},()=>{
                this.getData(studentNo);
            });
        }
    }
    getData(studentNo){
        let data = {studentNo}
        //学生信息
        postAction("/bigdata/student/getPageData", data).then(
            (res) => {
                if(res.success) {
                    this.setState({studentInfo: res.obj.list[0]})
                } else {
                    message.error(res.msg);
                }
            }
        )
        //历史预警
        postAction("/bigdata/alarmTrigger/getStudentAlarmHistory", data).then(
            (res) => {
                if(res.success) {
                    let historyAlarms = res.obj.map(item=>{
                        return {
                            date: item.createMonth,
                            '网络': item.alarmNetwork,
                            '消费': item.alarmConsume,
                            '夜归': item.alarmBack,
                            '逃课': item.alarmCourse,
                            '挂科': item.alarmFail,
                            '失联': item.alarmMiss,

                        }
                    })
                    this.setState({historyAlarms})
                } else {
                    message.error(res.msg);
                }
            }
        )

    }
    render(){
        const data = this.state.historyAlarms || [];
        return (
            <React.Fragment>
                <Row gutter={20} type={'flex'}>
                    <Col sm={16} xl={17} xxl={18}>
                        <CurEarlyWarnings studentNo={this.state.studentNo}/>
                        <div style={{marginTop:20}}>
                            <LineChartCard style={{height:382}}
                                title={(
                                    <div className="common-title">历史预警<span style={{color: '#4A4A4A',marginLeft: '8px'}}>单位：次</span></div>
                                )}
                                data={data}
                                height={304}
                                transpose={true}
                                colors={['#1890FF','#13C2C2','#2FC25B','#FACC14','#F5A623','#F04864']}
                            />
                        </div>
                        <div style={{marginTop: 20}}>
                            <PersonalHistory studentNo={this.state.studentNo} />
                        </div>
                    </Col>
                    <Col sm={8} xl={7} xxl={6}>
                        <Card style={{height:'100%'}}>
                            <Info studentInfo={this.state.studentInfo}/>
                        </Card>
                    </Col>
                </Row>
            </React.Fragment>
        )
    }
}

export default withRouter(EarlyWarningDetail);


